$white: #f9ffff;

$theme-colors: (
    "light":      #ddf0f6,
    "dark":       #050000,
    "primary":    #313233,
    "secondary":  #7452e5,
    "info":       #eeb0d0,
    "success":    #22a55c,
    "warning":    #e6ca22,
    "danger":     #f3111a,
);

/* Remove the navbar's default margin-bottom and rounded borders */ 
.navbar {
    margin-bottom: 0;
    border-radius: 0;
    }
    
    /* Add a gray background color and some padding to the footer */
footer {
    background-color: #f2f2f2;
    padding: 25px;
}


.jumbotron { 
    background-image: url(../inspiration/37550928362_040bbaaac8_o.jpeg);
    background-color: none;
  }